<template>
    <div id="app" class="app">
        <div class="card__wrapper">
            <div class="card__title">
                <span>word: </span>
                <span>{{this.selectWords.length}}</span>
                <span> is </span>
                <span>{{this.getWords.length}}</span>
            </div>
            <div class="card__section card__section_main" v-if="!wordsEnd">
                <Card :word="this.selectWords[this.startCount]" :showTranslate="show" v-if="!allWords"/>
            </div>
            <div class="cars__error" v-if="wordsEnd">
                <p>Слова закончились</p>
            </div>
            <div class="card__section">
                <div class="allWords__wrapper" v-if="allWords">
                    <div class="allWords" v-for="(item) in this.getWords" v-bind:key="item">
                        <p @click="showTranslate">{{item.eng}}</p>
                        <p>{{item.rus}}</p>
                    </div>
                </div>
            </div>
            <div class="card__section card__section_btn" :class="allWords ? 'active' : ''">
                <div class="card__btn-wrapper" v-if="!allWords">
                    <button @click="prevWord" class="card__btn" v-if="!countNo">Back</button>
                    <button @click="nextWord" v-if="!wordsEnd" class="card__btn">Next</button>
                </div>
                <button @click="showAllWords">Show all</button>
            </div>
        </div>
    </div>
</template>

<script>
    import Card from './components/Card.vue'
    import {mapActions, mapGetters} from "vuex";
    import words from './data/words';

    export default {
        name: 'App',
        components: {
            Card
        },
        data() {
            return {
                startCount: 0, // Счетчик для пролиствования слов
                selectWords: [], // Активные слова (которые просмотрели)
                allWords: false, // Показать все слова
                wordsEnd: false, // Если слова закончились
                show: false, // Показать перевод
                countNo: false // Если слова закончились  
            }
        },
        //Отработает раньше всех
        computed: {
            ...mapGetters(['getWords']),
        },
        mounted() {
            //Перемещаем массив в случайном порядке
            function makeRandomArr() {
                return Math.random() - 0.5;
            }

            words.sort(makeRandomArr);
            this.$store.dispatch("actResponseWords", words);
            if (this.getWords) {
                this.selectWords.push(this.getWords[this.startCount]);
            }
        },
        //Methods
        methods: {
            ...mapActions(['actResponseWords']),
            nextWord() {
                this.startCount++;
                this.selectWords.push(this.getWords[this.startCount]);
                if (this.selectWords.length === this.getWords.length + 1) {
                    this.wordsEnd = true
                }
                this.show = false;
                this.countNo = false;
            },
            prevWord() {
                this.startCount--;
                this.selectWords.pop();
                if (this.selectWords.length !== this.getWords.length + 1) {
                    this.wordsEnd = false
                }
                if (!this.selectWords.length) {
                    this.countNo = true
                }
            },
            showAllWords() {
                this.allWords = !this.allWords;
            },
        }
    }
</script>

