import Vue from "vue";
import Vuex from 'vuex'; //npm install vuex --save

Vue.use(Vuex);

//Основной store
export default new Vuex.Store({
    //Data
    state: {
        words: []
    },
    //Actions
    actions: {
        actResponseWords({commit}, payload) {
            commit('mutResponseWords', payload)
        }
    },
    //Mutations
    mutations: {
        mutResponseWords (state, payload) {
            state.words = payload;
        }
    },
    //Getters
    getters: {
        getWords(state) {
            return state.words;
        }
    }
})