<template>
    <div class="card">
        <p @click="visibleTranslate" class="card__word">{{word.eng}}</p>
        <transition name="fade">
            <p v-if="showTranslate" class="card__translate">{{word.rus}}</p>
        </transition>
    </div>
</template>

<script>
    export default {
        name: 'Card',
        //Props
        props: {
            word: Object,
            showTranslate: Boolean
        },
        data() {
            return {
                show: false
            }
        },
        methods: {
            visibleTranslate() {
                this.showTranslate = !this.showTranslate;
            }
        }

    }
</script>

